
@font-face {
  font-family: digital;
  src: url("./font/digital-7.ttf");
}

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp,small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1.2;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
ol {
  padding-left: 1.4em;
  list-style: decimal;
}
ul {
  padding-left: 1.4em;
  list-style: square;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
a {
  text-decoration: none;
  color: #074456;
}
input,
input:active,
input:focus {
  outline: none;
}

html {
  font-size: 30px;
}

p {
  font-size: 20px;
  color: green;
}

body {
  background-image: url("./img/bg.png");
}

#calcHousing {
  position: relative;
  width: 11rem;
  height: 12.2rem;
  border: 1px solid black;
  margin: 30px;
  border-radius: 7%;
  background:#464343 url(data:image/png;base64,);
  box-shadow: -2px 0 2px #fff, -2px 4px 1px #fff, 0 0 1px #fff, 20px -40px 30px #000, 0 0 101px #000, 36px 0 32px #000;
  overflow: hidden;
}

#note {
  position: absolute;
  top: 30px;
  left: 30px;
  z-index: 1;
}

#output {
  position: absolute;
  width: 9.4rem;
  padding: 0 10px;
  height: 3rem;
  border: none;
  left: 0.5rem;
  top: 0.5rem;
  font-family: digital, serif;
  font-size: 1rem;
  line-height: 2.5rem;
  text-align: right;
  border-radius: 15px;
  margin-right: 15px;
  overflow: hidden;
  background-color: rgb(155, 155, 154);
}

.shadowOn {
  -webkit-box-shadow: inset 8px -8px 4px -5px rgba(87,67,87,1),inset -8px 8px 4px -5px rgba(87,67,87,1);
  -moz-box-shadow: inset 8px -8px 4px -5px rgba(87,67,87,1),inset -8px 8px 4px -5px rgba(87,67,87,1);
  box-shadow: inset 8px -8px 4px -5px rgba(87,67,87,1),inset -8px 8px 4px -5px rgba(87,67,87,1);
}

#mirror {
  position: absolute;
  width: 10rem;
  height: 3rem;
  border: none;
  left: 0.5rem;
  top: 0.5rem;
  border-radius: 15px;
  background-color: rgba(244, 254, 5, 0.31);
  z-index: 2;
}

#line {
  position: absolute;
  opacity: 0.5;
  width: 10rem;
  height: 8px;
  border: none;
  left: 0.5rem;
  top: 4.2rem;
  background: rgb(149,149,149);
  background: -moz-linear-gradient(top, rgba(149,149,149,1) 0%, rgba(13,13,13,1) 19%, rgba(1,1,1,1) 44%, rgba(1,1,1,1) 44%, rgba(1,1,1,1) 51%, rgba(78,78,78,1) 84%, rgba(10,10,10,1) 86%, rgba(10,10,10,1) 86%, rgba(56,56,56,1) 87%, rgba(27,27,27,1) 90%, rgba(27,27,27,1) 90%);
  background: -webkit-linear-gradient(top, rgba(149,149,149,1) 0%,rgba(13,13,13,1) 19%,rgba(1,1,1,1) 44%,rgba(1,1,1,1) 44%,rgba(1,1,1,1) 51%,rgba(78,78,78,1) 84%,rgba(10,10,10,1) 86%,rgba(10,10,10,1) 86%,rgba(56,56,56,1) 87%,rgba(27,27,27,1) 90%,rgba(27,27,27,1) 90%);
  background: linear-gradient(to bottom, rgba(149,149,149,1) 0%,rgba(13,13,13,1) 19%,rgba(1,1,1,1) 44%,rgba(1,1,1,1) 44%,rgba(1,1,1,1) 51%,rgba(78,78,78,1) 84%,rgba(10,10,10,1) 86%,rgba(10,10,10,1) 86%,rgba(56,56,56,1) 87%,rgba(27,27,27,1) 90%,rgba(27,27,27,1) 90%);

}

#secondWrap {
  display: block;
  position: relative;
  top: 4rem;
  left: 0.5rem;
  width: 10.1rem;
  height: 6.2rem;
  margin-left: 0.2rem;
}

.justButton {
  display: inline-block;
  width: 2rem;
  height: 1rem;
  border: none;
  margin-right: 0.3rem;
  margin-bottom: 0.3rem;
  background-color: black;
  color: white;
  font-size: 20px;
  vertical-align: top;
  border-radius: 40%;
  cursor: pointer;
  box-shadow: -2px 0 2px #fff, -2px 4px 1px #fff,
  0 0 1px #fff, 5px 1px 20px #000,
  0 0 2px #000, 2px 0 20px #000;
}

.justButton:active {
  box-shadow: -1px 0 1px #fff, -1px 1px 1px #fff,
  0 0 1px #fff, 1px 1px 1px #000,
  0 1px 1px #000, 1px 0 1px #000;
}

#on {
  background: url("./img/Power.png") no-repeat center;
}

#on , #mutePlay{
  cursor: pointer;
  position: relative;
  top: 3.7rem;
  left: 1rem;
  width: 1.2rem;
  height: 1.2rem;
  background-color: #4ac722;
  border-radius: 50%;
  font-size: 15px;
  border:none;
  box-shadow: -2px 0 2px #fff, -2px 2px 1px #fff,
  0 0 1px #fff, 5px 1px 20px #000,
  0 0 2px #000, 2px 0 20px #000;
}

#mutePlay {
  position: absolute;
  left: 155px;
  width: 5rem;
  margin-top: 10px;
  border-radius: 5px;
  height: 0.8rem;
  background-color: greenyellow;
  font-size: 15px;
}

#on:active , #mutePlay:active{
  box-shadow: -1px 0 1px #fff, -1px 1px 1px #fff,
  0 0 1px #fff, 1px 1px 1px #000,
  0 1px 1px #000, 1px 0 1px #000;
}

#minus {
  font-size: 20px;
}

#plus {
  position: absolute;
  width: 2rem;
  height: 2rem;
  margin-left: 0.2rem;
  margin-top: 0.2rem;
  background-color: #4ac722;
  color: black;
  font-size: 35px;
  border-radius: 40%;
  border: none;
  box-shadow: -2px 0 2px #fff, -2px 2px 1px #fff,
  0 0 1px #fff, 5px 1px 20px #000,
  0 0 2px #000, 2px 0 20px #000;
}

#plus:active {
  box-shadow: -1px 0 1px #fff, -1px 1px 1px #fff,
  0 0 1px #fff, 1px 1px 1px #000,
  0 1px 1px #000, 1px 0 1px #000;
}

.nondisplay {
  display: none;
}

.display {
  display: block;
}

.calculator-wrapper {
  width: 100%;
  display: flex;
}